import $ from 'jquery'

class Notes {
  constructor() {
    this.deleteBtn = document.querySelectorAll(".delete-note");
    // console.log(this.deleteBtn);
    this.events();
  }
  
  events() {
    
    // const deleteBtn =  Array.from(this.deleteNote);
    this.deleteBtn.forEach( this.deleteAction(btn))
    // deleteBtn.map( btn => {
    //   btn.addEventListener("click", deleteNote);
    // })
  }

  // Methods
  deleteAction(btn){
      this.btn.addEventListener("click", (e) => {
        console.log(this.deleteBtn);
        // e.preventDefault()
        this.deleteNote(e);
      })
  }
  
  
  deleteNote(e) {
    let thisNote = $(e.target).parent('li');
      $.ajax({
        beforeSend: (xhr) => {
          xhr.setRequestHeader('X-WP-Nonce', themeData.nonce);
        },
        url: themeData.root_url + '/wp-json/wp/v2/note/' + thisNote.data('id'),
        type: 'DELETE',
        success: (response) => {
          thisNote.slideUp();
          console.log('success');
          console.log(response);
        },
        error: (response) => {
          console.log('error');
          console.log(response);
        },
      })
  }
 }

 export default Notes;